*{
  margin: 0;
  padding: 0;
}
body{
  height: 100vh;
  overflow-y: hidden;
  background-color: #fff;
  overflow-x: hidden;
}
.chat-header{
  height: 60px;
  /*border-bottom: 1px solid #f1f1f1;*/
  display: flex;
  align-items: center;
  justify-content: space-around;
  box-sizing: border-box;
  background-color: #1C1C1D;
}
.chat-header-name{
  color: #FFFFFF;
}
.chat-header-name > img{
  width: 20px;

}
.chat-header-back{
  text-decoration: none;
  color: #FFFFFF;
}
.chat-body{
  display: flex;
  flex-direction: column;
  height: calc(100vh - 47px - 60px);
  overflow-y: scroll;
  overflow-x: hidden;
  padding-bottom: 10px;
  background-color: #fff;
  background: url("images/background.png");
  background-repeat: repeat;
  background-position: center center;
  background-size: cover;
}
.chat-footer{
  height: 47px;
  display: flex;
  align-items: center;
  /*box-shadow: inset 0px 1px 0px #E5E5EA;*/
  /*border-top: 1px solid #f1f1f1;*/
  width: 100%;
  background-color: #1C1C1D;
  padding: 6px 15px;
  box-sizing: border-box;
}
.prev-page{
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
}
.prev-page > svg{
  display: block;
  height: 15px;
}
.prev-page > span{
  display: block;
  text-decoration: none;
  margin-left: 5px;
  margin-bottom: 2px;
}
.chat-footer > input{
  outline: none;
  border: none;
  display: block;
  height: 33px;
  background-color: #060606;
  border: 1px solid #3A3A3C;
  border-radius: 16.5px;
  width: 100%;
  color: #f1f1f1;
  box-sizing: border-box;
  padding-left: 10px;
  padding-right: 10px;
}
.chat-footer > button{
  display: none;
  width: 10%;
  height: 100%;
  outline: none;
  border: none;
  box-sizing: border-box;
  background-color: #fff;
  border-left: 1px solid #f1f1f1;
  display: none;
  justify-content: space-around;
  align-items: center;
}
.chat-body-item{
  display: flex;
}
.chat-body-item > .chat-body-item-text{
  background-color: #262628;
  padding: 4px 8px 4px 0px;
  border-radius: 0px 6px 6px 0px;
  margin-top: 16px;
  max-width: 80%;
  color: #ffffff;
  padding-left: 10px;
  display: flex;
  flex-direction: column;
}
.chat-body-item.right > .chat-body-item-text{
  background-color: #313131;
  padding: 0;
  padding: 4px 0px 4px 8px;
  border-radius: 0;
  border-radius: 6px 0px 0px 6px;
  color: #fff;
  padding-left: 8px;
  padding-right: 10px;

}
.chat-body-item.right{
  justify-content: end;
}
.chat-body-item-datetime{
  font-size: 10px;
  font-weight: 300;
  display: block;
  text-align: right;
  margin-top: 5px;
  margin-bottom: 5px;
}

li{
  list-style: none;
}
body{
  background-color: #1C1C1E;
}
.chat-list-header{
  height: 50px;
  /*border-bottom: 1px solid #f1f1f1;*/
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #1C1C1E;
  color: #ffffff;
  font-weight: bold;

}
.chat-list{
  background-color: #1C1C1E;
}
.avatar{
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #f1f1f1;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.avatar > img{
  width: 30px;
}
.chat-list-item{
  margin: 0;
  padding: 15px;
  box-sizing: border-box;
  display: flex;
  /*border-bottom: 1px solid #f1f1f1;*/
  align-items: center;
  text-decoration: none;
  color: #000;
  border-bottom: 1px solid #494949;
}
.chat-list-item.new-messages{
  border-left: 4px solid red;
}
.chat-list-item > .name{
  margin-left: 10px;
  font-weight: normal;
  display: block;
  color: #fff;
}
.new-message-count{
  width: 50px;
  display: block;
}